@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@gridLine: 5px;
* {box-sizing: border-box}

html { 
  width:100%;
  height:100%;
}

body { 
  width:100%;
  height:100%;
  background: #09111e;
  background: black;
  overflow: hidden;
  font-family: 'Oswald', sans-serif;
  font-weight: 200;
}

.grid-wrapper {
  position:absolute;
  width:100%;
  height:100%;
  left:0;
  bottom:0px;
  -webkit-perspective:200px;
          perspective:200px;
}

.grid {
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100%;
  height:100%;
  margin-top:-50px;
  -webkit-transform: rotateX(45deg);
          transform: rotateX(45deg);
}

.line {
  position:absolute;
  background:rgba(87, 193, 232, 0.35);
  -webkit-backface-visibility: hidden;
}

.vertical .line {
  top:0;
  left:0;
  bottom:0;
  width:@gridLine;
  background: linear-gradient(top, rgba(255,255,255,0), rgba(87, 193, 232, 0.35));
  
  &:nth-child(1) {
    left:10%;
  }
  &:nth-child(2) {
    left:20%;
  }
  &:nth-child(3) {
    left:30%;
  }
  &:nth-child(4) {
    left:40%;
  }
  &:nth-child(5) {
    left:50%;
  }
  &:nth-child(6) {
    left:60%;
  }
  &:nth-child(7) {
    left:70%;
  }
  &:nth-child(8) {
    left:80%;
  }
  &:nth-child(9) {
    left:90%;
  }
  &:nth-child(10) {
    left:100%;
    margin-left:-1px;
  }
}

.horizontal .line {
  top:0;
  left:0;
  right:0;
  height:@gridLine;
  z-index:2;
  
  &:nth-child(1) {
    top:10%;
    opacity:0.2;
  }
  &:nth-child(2) {
    top:20%;
    opacity:0.3;
  }
  &:nth-child(3) {
    top:30%;
    opacity:0.4;
  }
  &:nth-child(4) {
    top:40%;
    opacity:0.5;
  }
  &:nth-child(5) {
    top:50%;
    opacity:0.6;
  }
  &:nth-child(6) {
    top:60%;
    opacity:0.7;
  }
  &:nth-child(7) {
    top:70%;
    opacity:0.8;
  }
  &:nth-child(8) {
    top:80%;
    opacity:0.9;
  }
  &:nth-child(9) {
    top:90%;
    opacity:0.95;
  }
  &:nth-child(10) {
    top:100%;
    margin-top:-1px;
    opacity:1;
  }
  &:nth-child(11) {
    opacity:0.05;
  }
}

.dot {
  position:absolute;
  background:#57C1E8;
  width: @gridLine;
  height: @gridLine;
  top:100%;
  border-radius: @gridLine;
  box-shadow: 0 0 3px #fff;
  -webkit-backface-visibility: hidden;
  &:nth-child(1) {
    left:10%;
    -webkit-animation: moveDot 5s infinite;
            animation: moveDot 5s infinite;
  }
  &:nth-child(2) {
    left:20%;
    -webkit-animation: moveDot 20s infinite;
            animation: moveDot 20s infinite;
  }
  &:nth-child(3) {
    left:30%;
    -webkit-animation: moveDot 15s infinite;
            animation: moveDot 15s infinite;
  }
  &:nth-child(4) {
    left:50%;
    -webkit-animation: moveDot 30s infinite;
            animation: moveDot 30s infinite;
  }
  &:nth-child(5) {
    left:60%;
    -webkit-animation: moveDot 12s infinite;
            animation: moveDot 12s infinite;
  }
  &:nth-child(6) {
    left:80%;
    -webkit-animation: moveDot 6s infinite;
            animation: moveDot 6s infinite;
  }
  &:nth-child(7) {
    left:90%;
    -webkit-animation: moveDot 8s infinite;
            animation: moveDot 8s infinite;
  }
}

@-webkit-keyframes moveDot {
  0%   { top: 100%; opacity:1; }
  100% { top: 0%; opacity:0; }
}

@keyframes moveDot {
  0%   { top: 100%; opacity:1; }
  100% { top: 0%; opacity:0; }
}
